.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.g,.w,.o,.y,.r,.b{
  width: 6pc;
  height: 6pc;
}

.g, .cg,.cug{
  background: #00ff00;

}

.w, .cw,.cuw{
  background: #ffffff;

}
.o, .co,.cuo{
  background: #ff5e00;
}
.y, .cy,.cuy{
  background: #ffff00;

}
.r, .cr,.cur{
  background: #ff0000;
}

.b, .cb,.cub{
  background: #0000ff;
}

.cg,.cw,.co,.cy,.cr,.cb{
  
  width: 10pc;
  height: 10pc;
  border:0.5pc solid black;
  border-radius: 3pc;
}
.cug,.cuw,.cuo,.cuy,.cur,.cub{
  width: 9pc;
  height: 9pc;
  border-radius: 3pc;
}

